/** Media Queries **/
html {
  font-size: 62.5%;
  box-sizing: border-box;
  height: 100%;
}

body {
  min-height: 100%;
  display: flex;
  flex-direction: column;
  background-color: #1a1b15;
  color: #FFFFFF;
  margin: 0;
}

*, *:before, *:after {
  box-sizing: inherit;
}

button {
  background-color: rgba(34, 128, 107, 0);
  border: none;
}

body {
  font-family: "Nunito", sans-serif;
  font-size: 1.6rem;
  background-color: #FFFFFF;
}

.contenedor {
  width: 95%;
  max-width: 1200px;
  margin: 0 auto;
}

a {
  text-decoration: none;
}

img {
  max-width: 100%;
}

ul {
  list-style: none;
}

h1,
h2,
h3 {
  font-family: "Nunito", sans-serif;
  margin: 0 0 2.5rem 0;
  font-weight: 900;
  color: #FFFFFF;
  text-align: center;
}

h1 {
  font-size: 4rem;
}

h2 {
  font-size: 3.6rem;
}

h3 {
  font-size: 3rem;
  text-align: center;
}

p {
  color: #FFFFFF;
  font-size: 2rem;
}

header {
  z-index: 1000;
  background-color: #22806b;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  height: fit-content;
  position: fixed;
  top: 0;
  width: 100%;
  z-index: 1000; /* Para asegurar que la barra de navegaci√≥n est√© sobre otros elementos */
  box-shadow: 0 10px 10rem rgba(0, 0, 0, 0.1); /* A√±adir sombra */
}
header .nombre {
  padding-top: 1rem;
  margin-bottom: 1rem;
}
header .nombre span {
  color: #1a1b15;
}
@media (min-width: 768px) {
  header {
    display: flex;
    flex-direction: column;
  }
}
header .menu {
  display: flex;
  flex-direction: column;
  align-items: center;
}
header .menu svg {
  width: fit-content;
}

.cats {
  margin: 1rem;
  padding: 0;
  text-align: center;
  display: flex;
  flex-direction: column;
  opacity: 0;
  display: none;
}
@media (min-width: 768px) {
  .cats {
    position: fixed;
    margin-top: 4rem;
  }
}
.cats li {
  margin-bottom: 2rem;
  padding: 0rem 2rem;
}
.cats li:hover {
  background-color: #22806b;
}
.cats li a {
  color: #1a1b15;
  font-size: 3rem;
}

.mostrar {
  display: block;
  opacity: 1;
  background-color: #FFFFFF;
  border: 0.1rem solid black;
  box-shadow: 5px 5px 10px rgba(0, 0, 0, 0.5);
  padding: 1rem 2rem;
  transition: opacity 1s ease;
}

.menu .menu1 {
  border: solid 5px transparent;
  border-radius: 5rem;
}

.menu1:hover {
  cursor: pointer;
  border: solid 5px transparent;
  background-color: rgba(128, 128, 128, 0.5);
  border-radius: 5rem;
}

.iconos {
  display: flex;
  justify-content: space-around;
  flex-direction: column-reverse;
  align-items: center;
  width: 100%;
}
@media (min-width: 768px) {
  .iconos {
    flex-direction: row;
  }
}

.carrito {
  display: flex;
  align-items: center;
}
.carrito p {
  font-size: 2.5rem;
  margin: 0;
}

.icon {
  cursor: pointer;
}

.banner {
  height: 30rem;
  margin-top: 55%;
}
@media (min-width: 768px) {
  .banner {
    margin-top: 10rem;
    height: 55rem;
  }
}

.banner-imagen {
  opacity: 0.7;
  padding: 0;
  margin: 0;
  height: 30rem;
  position: absolute;
}
@media (min-width: 768px) {
  .banner-imagen {
    height: 55rem;
  }
}

.banner-texto {
  position: relative;
  top: 15rem;
  margin: 0 auto;
}
.banner-texto h2 {
  padding: 0.7rem;
  font-size: 2rem;
  background-color: #1a1b15;
}
@media (min-width: 768px) {
  .banner-texto {
    text-align: center;
    top: 25rem;
    background-color: #1a1b15;
    width: fit-content;
  }
  .banner-texto h2 {
    font-size: 4rem;
  }
}

@media (min-width: 768px) {
  .items {
    margin-top: 2rem;
  }
}
@media (min-width: 768px) {
  .items {
    display: grid;
    gap: 3rem;
    grid-template-columns: repeat(2, 1fr);
  }
}

.destacados-titulo {
  color: #1a1b15;
}

.destacados {
  background-image: url("/public/build/img/Documento A4 Cuaderno Azul Liso (1).webp");
  background-size: 35rem;
  height: 20rem;
  width: 100%;
  background-repeat: repeat-y;
  background-position: center top;
  margin-bottom: 3rem;
}
@media (min-width: 768px) {
  .destacados {
    height: 55rem;
  }
}
@media (min-width: 768px) {
  .destacados {
    background-size: 50rem;
  }
}

.productos {
  display: grid;
  gap: 3rem;
  grid-template-columns: repeat(2, 1fr);
}

.producto {
  padding: 1rem;
  border: 1px solid grey;
  display: flex;
  flex-direction: column;
  align-items: center;
}
.producto .producto-titulo {
  color: #22806b;
  font-weight: bold;
  font-size: 1.5rem;
}
@media (min-width: 768px) {
  .producto .producto-titulo {
    font-size: 3rem;
  }
}
.producto .producto-precio {
  color: #1a1b15;
  font-weight: bold;
  margin-top: 1rem;
}